function LoginForm() {

  const [users, setUsers] = React.useState([{ a: 'd' }]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    console.log(formData.get('username'), formData.get('password'));
  };

  React.useEffect(() => {
    fetch('http://localhost:5000/user/getall')
      .then(data => {
        // console.log(data.json());
        setUsers(data.json());
        setUsers({ a: 'ds' });
        console.log(users);
      })
  }, [])
  return (
    <div className="col-md-4 mx-auto">

      <div className="card mt-5">
        <div className="card-body">
          <p className="h3">Login Form</p>
          <hr></hr>

          <form method="POST">
            <label className="mt-4" htmlFor="username">Username</label>
            <input className="form-control" name="username" id="username"></input>
            <label className="mt-5" htmlFor="password">Password</label>
            <input className="form-control" name="password" type="password" id="password"></input>

            <button className="btn btn-primary btn-block mt-5">Submit</button>
          </form>
        </div>
      </div>
    </div>
  );
}

